body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.5;
  font-weight: 400;
  color: #000;
  text-transform: uppercase;
  background-color: #f2f2f2;
}
a {
  text-decoration: none;
}
p {
  margin: 0;
  padding: 0;
}
b {
  font-weight: 400;
}
img {
  max-width: 100%;
  height: auto;
  display: block;
}
.wrapper {
  width: 320px;
  margin: 0 auto;
  box-shadow: 0 8px 11px 2px #ccc;
  background-color: #fff;
}
@media (min-width: 768px) {
  .wrapper {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .wrapper {
    width: 1200px;
  }
}
.title {
  text-align: center;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  cursor: default;
  margin: 0;
}
.title--left {
  text-align: left;
}
.title--big {
  font-size: 21px;
  line-height: 26px;
  padding-bottom: 19px;
}
@media (min-width: 768px) {
  .title {
    font-size: 21px;
    line-height: 26px;
  }
  .title--left {
    text-align: center;
  }
  .title--big {
    padding: 0;
    font-size: 30px;
    line-height: 26px;
    margin-bottom: 30px;
  }
}
@media (min-width: 1200px) {
  .title--left {
    margin-top: 0;
  }
  .title--big {
    margin-bottom: 27px;
  }
}
.btn {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  margin: 0 auto;
  color: #fff;
  text-transform: uppercase;
  border: none;
  background-color: #81b3d2;
  padding-top: 18px;
  padding-bottom: 15px;
  padding-left: 46px;
  padding-right: 47px;
  cursor: pointer;
}
.btn:hover {
  background-color: #669ec0;
  color: #fff;
}
.btn:active {
  background-color: #5496bd;
  color: #94b9d2;
}
.btn:disabled {
  background-color: #e5e5e5;
  color: #fff;
}
@media (min-width: 768px) {
  .btn {
    padding-top: 16px;
    padding-bottom: 17px;
    padding-left: 136px;
    padding-right: 136px;
  }
}
.main-nav {
  position: relative;
  background-color: #81b3d2;
}
.main-nav__toggle {
  position: absolute;
  right: 20px;
  top: 17px;
  padding: 0;
  z-index: 2;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  fill: #fff;
}
.main-nav__toggle:hover svg {
  fill: #b3d1e4;
}
.main-nav__toggle:active svg {
  fill: #d9e8f1;
}
.main-nav--closed .main-nav__toggle {
  display: none;
}
.main-nav--closed .main-nav__item {
  display: none;
}
.main-nav--opened .main-nav__menu {
  display: none;
}
.main-nav--opened .main-nav__wrapper {
  box-shadow: inset 0 -2px 7px -2px rgba(0, 0, 0, 0.4);
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}
.main-nav--nojs .main-nav__item {
  display: block;
}
.main-nav__list {
  color: #81b3d2;
  list-style-type: none;
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 40px;
}
.main-nav__item {
  color: #fff;
  text-align: center;
  padding-top: 8px;
  padding-bottom: 6px;
  display: block;
  border-bottom: 1px solid #678fa8;
  border-top: 1px solid #9ac2db;
}
.main-nav__item--top {
  border-top: none;
}
.main-nav__item--bottom {
  border-bottom: none;
}
.main-nav__item a {
  color: #fff;
  display: inline-block;
}
.main-nav__item:hover {
  background-color: #669ec0;
  border-top-color: #669ec0;
}
.main-nav__item:active {
  background-color: #5496bd;
  border-top-color: #5496bd;
}
.main-nav__item:active a {
  color: #94b9d2;
}
.main-nav__white {
  position: relative;
  min-height: 55px;
  background-color: #fff;
}
.main-nav__menu {
  position: absolute;
  top: 17px;
  right: 13px;
  border: none;
  outline: none;
  background-color: transparent;
  fill: #81b3d2;
}
.main-nav__menu:hover svg {
  fill: #b3d1e4;
}
.main-nav__menu:active svg {
  fill: #d9e8f1;
}
@media (min-width: 768px) {
  .main-nav {
    background-color: #fff;
  }
  .main-nav__list {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
    font-weight: 700;
    font-size: 14px;
    line-height: 26px;
  }
  .main-nav__item {
    width: 24%;
    box-sizing: border-box;
    padding-top: 15px;
    padding-bottom: 15px;
    border-top: none;
    border-bottom: none;
  }
  .main-nav__item:nth-child(3n),
  .main-nav__item:nth-child(3n+1) {
    text-align: right;
    background-color: #fff;
  }
  .main-nav__item:nth-child(2) {
    text-align: left;
    background-color: #fff;
    padding-left: 6px;
  }
  .main-nav__item:nth-child(3) {
    text-align: right;
    background-color: #fff;
    margin-left: 65px;
  }
  .main-nav__item:nth-child(4n) {
    background-color: #fff;
    padding-right: 27px;
  }
  .main-nav__item:first-child,
  .main-nav__item:nth-child(5n) {
    padding-left: 49px;
    text-align: left;
    background-color: #fff;
  }
  .main-nav__item a {
    color: #000;
    cursor: pointer;
  }
  .main-nav__item:hover a {
    color: #81b3d2;
  }
  .main-nav__item:active a {
    color: #b2b2b2;
  }
  .main-nav__item--active a {
    color: #766357;
  }
  .main-nav__item--active:hover a {
    color: #604e43;
  }
  .main-nav__item--active:active a {
    color: #d6d0cc;
  }
  .main-nav__white {
    display: none;
  }
  .main-nav__toggle svg {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__item {
    width: 25%;
    box-sizing: border-box;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .main-nav__item:nth-child(3n),
  .main-nav__item:nth-child(3n+1) {
    text-align: right;
  }
  .main-nav__item:nth-child(2) {
    text-align: left;
    padding-left: 24px;
  }
  .main-nav__item:nth-child(3) {
    padding-right: 23px;
  }
  .main-nav__item:first-child,
  .main-nav__item:nth-child(5n) {
    padding-left: 90px;
    text-align: left;
  }
  .main-nav__item:nth-child(4n) {
    padding-right: 71px;
  }
}
